import User from './User'
import React from 'react'
import {Component} from 'react'
import axios from 'axios'
import storeFactory from '../store'
import {serverLink} from '../constants'
import HeadComp from './HeadComp'

export default class UserStore extends Component {

    state = {loading: 1}
    user = null
    incomingData = null


    static getInitialProps({ query: { id } }) {

        return id
    }

    async componentDidMount() {

        this.incomingData = await axios(serverLink + '/data/' + id)
        this.setState({loading: 0})
    }

    render() {

        if (!this.state.loading) {

            const {products, customerMeta, customerML} = incomingData
            const store = storeFactory({products, customerMeta, customerML})
            this.user = this.props.id
        }


        return (
            <div>
                <HeadComp/>
                {!this.state.loading ? <User store={store}/> : <p/>}
            </div>
        )
    }
}


